
header {
  height: 50vh;
  padding-top: 7rem;
  overflow: hidden;
  text-align: center;
  align-items: center;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}
/* ============ CTA ============ */
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}
